.error-tip {
  display: inline-block;
  vertical-align: top;
  padding-right: 20px;
  height: 26px;
  line-height: 26px;
  position: relative;
  color: #ea3323;
}
.error-tip:after {
  content: "";
  width: 16px;
  height: 16px;
  background: url('/assets/images/close.png') no-repeat;
  background-size: 100% 100%;
  position: absolute;
  top: 50%;
  margin-top: -8px;
  right: 0;
}
.checkbox {
