}
.table th,
.table td {
  text-align: center;
}
.table tr,
.table .tr {
  border-bottom: 1px solid #b5b5b5;
}
.table .tr:nth-child(odd) {
  background-color: #FCFCFC;
}
.table tr:nth-child(even) {
  background-color: #FCFCFC;
}
.table .tr {
  width: 100%;
