.tab {
  display: block;
  width: 100%;
  background-color: #FCFCFC;
  height: 54px;
  border-bottom: 1px solid #b5b5b5;
  border-top: 1px solid #b5b5b5;
  position: relative;
  white-space: nowrap;
}
.tab .container {
  position: absolute;
  left: 0px;
  right: -66px;
  height: 100%;
  padding-left: 30px;
  font-size: 0;
}
.tab .item {
  margin-right: 66px;
  display: inline-block;
  color: #0a0a0a;
  padding: 0 10px;
  line-height: 49px;
  font-size: 18px;
  border-bottom: 3px solid transparent;
  cursor: pointer;
}
.tab .item.active {
  color: #ea3323;
  border-bottom: 3px solid #ea3323;
}
.redpack {
  position: relative;
  display: inline-block;
  width: 276px;
  height: 108px;
  margin-top: 5px;
  margin-right: 20px;
  padding: 20px 15px 0;
  color: #fff;
  background: url('/assets/images/redpack.png') no-repeat;
  background-size: 100% 100%;
  cursor: pointer;
  overflow: hidden;
}
.redpack .amount {
  font-size: 30px;
  position: absolute;
  top: 11px;
  right: 16px;
  color: #ffffff;
}
.redpack i {
  font-size: 18px;
  display: inline-block;
  margin-top: 11px;
  margin-left: 2px;
  vertical-align: top;
  color: #ffffff;
}
.redpack p {
  color: #ffffff;
  white-space: normal;
  word-break: break-all;
  word-wrap: break-word;
  font-size: 12px;
}
.redpack.disabled {
  background-image: url("/assets/images/redpack-disabled.png");
  cursor: not-allowed;
}
.redpack.disabled p {
  color: #7a7a7a;
}
.redpack.disabled .amount {
  color: #7a7a7a;
}
.redpack.disabled i {
  color: #7a7a7a;
}
.redpack.received:after {
  position: absolute;
  top: 8px;
  right: 56px;
  width: 55px;
  height: 55px;
  content: "";
  background: url('/assets/images/received.png') no-repeat;
  background-size: 100% 100%;
}
